@import "@/assets/css/main.scss";








@import '@/assets/css/main.scss';
@import '@/template-groovy/assets/css/groovy-main.scss';

.quoteBlock {
  background: darken($accent-red, 10);
  // margin-bottom: .5em;
  padding: .75em 2em 1em;
  @include shadow;
  // @include clearfix;
  text-align: center;

  p {
    font-family: $font-copy !important;
    // font-family: "Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
    font-size: 0.8em;
    color: $ivory !important;
    text-align: center;
    letter-spacing: .08em;
    font-weight: bold;
    line-height: 2rem !important;
  }

  cite {
    font-family: $font-copy !important;
    // font-family: "Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
    font-size: .7em;
    color: $ivory;
    font-style: italic;
    margin: 0 4em 0 0;
    display: block;
    text-align: right;
    letter-spacing: .08em;
  }
}
