// BREAKPOINTS
$XLargeScreen: 1200px;
$LargeScreen:  1024px;
$MidScreen:    768px;
$MidScreenSm:  700px;
$SmallScreen:  480px;
$XSmallScreen: 320px;

// FONTS
$base-font-size   :1rem;
$base-line-height :1.7em;
$font             :"Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
$font-head        :"Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
$font-subhead     :"Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
$font-copy        :"Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif !important;

// ############################

body {
  font-family: $font;
  font-size: $base-font-size;
  background: #333;
  margin: 0;
}
