  .car {
    background: url(/img/alfa-carabo.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 226px;
    height: 165px;
    display: block;
    position: relative;
    top: -0.3em;
    left: 0.7em;

    @media (max-width: $breakThree) {
      width: 186px;
      height: 135px;
      top: 1em;
      left: -1em;
    }

    // IPHONE 6,7,8
    @media only screen
    and (min-device-width: 375px)
    and (max-device-width: 667px)
    {
      width: 186px;
      height: 135px;
      top: 1em;
      left: -1em;
      // background: red;
      // border: 10px green solid;
    }
  }

  .starWars {
    background: url(/img/star-wars-transfers.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 148px;
    height: 177px;
    display: block;
    position: relative;
    top: -182px;
    right: 0em;
    float: right;

    @media (max-width: $breakThree) {
      width: 120px;
      height: 177px;
      top: -152px;
      right: 0em;
    }

